/* Header.module.css - 커스텀 헤더 스타일 */

.header {
  background-color: white;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.1);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 16px;
}

@media (min-width: 640px) {
  .container {
    padding: 0 24px;
  }
}

@media (min-width: 1024px) {
  .container {
    padding: 0 32px;
  }
}

.nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 64px;
}

.logo {
  display: flex;
  align-items: center;
}

.logoText {
  font-size: 24px;
  font-weight: 700;
  color: #1f2937;
}

.desktopNav {
  display: none;
  gap: 32px;
}

@media (min-width: 768px) {
  .desktopNav {
    display: flex;
  }
}

.navLink {
  color: #374151;
  padding: 8px 12px;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  transition: color 0.2s ease;
}

.navLink:hover {
  color: #1f2937;
}

.authButtons {
  display: none;
  align-items: center;
  gap: 16px;
}

@media (min-width: 768px) {
  .authButtons {
    display: flex;
  }
}

.loginButton {
  color: #374151;
  padding: 8px 12px;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  transition: color 0.2s ease;
}

.loginButton:hover {
  color: #1f2937;
}

.signupButton {
  background-color: #2563eb;
  color: white;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  transition: background-color 0.2s ease;
}

.signupButton:hover {
  background-color: #1d4ed8;
}

.mobileMenuButton {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 8px;
  border-radius: 6px;
  color: #374151;
  background: none;
  border: none;
  cursor: pointer;
  transition: all 0.2s ease;
}

@media (min-width: 768px) {
  .mobileMenuButton {
    display: none;
  }
}

.mobileMenuButton:hover {
  color: #1f2937;
  background-color: #f3f4f6;
}

.mobileMenuButton:focus {
  outline: 2px solid #2563eb;
  outline-offset: 2px;
}

.mobileMenu {
  display: block;
  border-top: 1px solid #e5e7eb;
  background-color: white;
}

@media (min-width: 768px) {
  .mobileMenu {
    display: none;
  }
}

.mobileNavContainer {
  padding: 8px 8px 12px;
  gap: 4px;
}

@media (min-width: 640px) {
  .mobileNavContainer {
    padding-left: 12px;
    padding-right: 12px;
  }
}

.mobileNavLink {
  color: #374151;
  display: block;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  transition: color 0.2s ease;
}

.mobileNavLink:hover {
  color: #1f2937;
}

.mobileAuthButtons {
  padding: 8px 12px;
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.mobileLoginButton {
  display: block;
  color: #374151;
  padding: 8px 12px;
  font-size: 16px;
  font-weight: 500;
  text-decoration: none;
  transition: color 0.2s ease;
}

.mobileLoginButton:hover {
  color: #1f2937;
}

.mobileSignupButton {
  display: block;
  width: 100%;
  background-color: #2563eb;
  color: white;
  padding: 8px 16px;
  border-radius: 6px;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
  text-align: center;
  transition: background-color 0.2s ease;
}

.mobileSignupButton:hover {
  background-color: #1d4ed8;
}